import React from 'react'
import Link from 'gatsby-link'
import District from '../../components/District'

const DistrictNo5 = ({ data }) => {
  return <District nodes={data.allLocationsXlsxSheet1.edges} />
}

export default DistrictNo5

export const DistrictNo5Query = graphql`
  query DistrictNo5Query {
    allLocationsXlsxSheet1(filter: { district: { eq: "5" } }) {
      edges {
        node {
          plf_id
          complete_address
          longitude
          latitude
          city
          ward
          district
          planned_launch_date
          week
          status
          contact_email
          notes
        }
      }
    }
  }
`



